import styles from './Projects.module.css'
import ProjectCard from '../projects/ProjectCard'

const Projects = () => {
    return (
        <div className={styles.projects}>
            <h1>Projetos</h1>
            <p>Alguns dos meus projetos e mdulos pessoais:</p>
            <div>
            <ul>
                <li>
                    <ProjectCard 
                        link ='http://isbnowf.tiagomelo.kinghost.net/' 
                        title='ISBNow' 
                        desc='Mdulo de cadastro de livros utilizando autopreenchimento de dados via cdigo ISBN com integrao entre APIs externas.' 
                        techs='ReactJS, JavaScript, Node, Sequelize, Express e MySQL' 
                        arch='Backend com arquitetura MVC e padro REST. Frontend projetado em React.'
                    />
                </li>                
                <li>
                    <ProjectCard
                        title = 'InMail'
                        desc = 'Microservio para comunicao interna via mensagens privadas e/ou pblicas com gerenciamento de correspondncias eletrnicas semelhante a um servidor de Email.'
                        techs = 'PHP e MySQL'
                        arch = ''
                    />
                </li>
                <li>
                    <ProjectCard
                        title = 'ShowU'
                        desc = 'Sistema desenvolvido para um portiflio minimalista com pgina para amostra de projetos, contatos, pgina sobre o desenvolvedor e alguns efeitos visuais sutis.'
                        techs = 'ReactJS, CSS e HTML.'
                        arch = 'Arquitetura componentizada do ReactJS estruturada em Layouts, Pages e Models.'
                    />
                </li>
            </ul>
            </div>
        </div>
    )
}

export default Projects